import React from 'react'
import Logo from '../../../assets/images/Logo.svg';

const MainHeader = (props) => {
    return (
        <div className="header">
            <div class='logo'>
                <img src={Logo}/>
                <div className="header-text">BlockPortal</div>
                <div className="header-about">About</div>
                <div className="header-seed">Seed Round 1</div>
            </div>
            
            <div className="btn-init btn-connectWallet" 
                onClick={() => props.connectWallet()}>
                {props.walletAddress ? (props.walletAddress.slice(0, 6)+'....'+props.walletAddress.slice(37)) : 'Connect Wallet'}
                {props.walletAddress && (<div className='disconnect-board' onClick={() => props.disconnectWallet()}>disconnect</div>)}
            </div>
        </div>
    );
}

export default MainHeader;