import React from 'react'
import SubmitBoard from "./sub/SubmitBoard";
import ProgressBNB from "./sub/ProgressBNB";

const MainBody = (props) => { 
    return (
        <div className="middle">
                    <div className="middle-text-1">BlockPortal</div>
                    <div className="middle-text-2">Invest<br></br>Track<br></br>Network</div>
                    <div className="middle-text-4">Private Sale | Seed Round 1</div>
                    <div className="middle-text-5">Launching Q4 2022</div>
                    <ProgressBNB history={props.history}/>
                    <SubmitBoard 
                        fullName = { props.fullName }
                        telegram = { props.telegram }
                        twitter = { props.twitter }
                        amount={ props.amount }
                        submit = {() => {props.submit()}}
                        changeValue = {(obj) => {props.changeValue(obj)}}
                    />
        </div>
    )
}

export default MainBody;