import React from 'react'
import { Chart as ChartJS, ArcElement, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';

ChartJS.register(ArcElement, Legend);

export default function CorporateHolding() {
  const data = {
    datasets: [
      {
        label: '# of Votes',
        data: [10, 90],
        backgroundColor: [
          '#FFFFFF',
          'rgba(0, 87, 255, 0.94)',
        ],
        borderColor: [
          '#FFFFFF',
          '#FFFFFF',
        ],
        borderWidth: 1,
      },
    ]
  }
  return (
    <div className='corporate_holding'>
      <div className='pie_chart'>
          <Pie data={data} />
          <div className='chart_pro'>90%</div>
      </div>
      <div className="token_text_4">90% Coorporate Holdings<br/><br/>{'\u2022'} Coorporate Use<br/>{'\u2022'} Listing Use</div>
    </div>
  )
}
