import React from 'react'
import { Table } from 'react-bootstrap'
import { useState, useEffect } from 'react'
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const HistoryTable = (props) => {
    const [current_page, setCurrentPage] = useState(1);
    const [from, setFrom] = useState(0);
    const [to, setTo] = useState(10);

    var result = [];
    var i = from;

    // if(i >= 0) {
        while(true) {
            if(i === (to-1) || i >= props.history.length) {
                break
            }
            var temp = {...props.history[i]}
            result.push({...temp})
            i++;
            
        }
    // }

    useEffect(() => {
        // result = [];
        let from = (current_page-1)*10;
        let to = current_page*10;
        setFrom(from);
        setTo(to);
    }, [current_page])
    


    var front = 4
    const { innerWidth: width, innerHeight: height } = window;

    if(width <= 1400 && width >= 970) {
        front = 5
    }

    let paginationConfig = {
        count: parseInt(props.history.length == 0 ? 1 : (props.history.length-1)/10 + 1),
        shape:"round",
        color:"primary",
        onChange: function (e, page) {
           setCurrentPage(page);
         }
      };

    return (
        <div className='display-table'>
            <div className="table-board">
                <Row>
                    <Table style={{ width: '100%'}}>
                        <thead>
                            <tr>
                                <th style={{ width: '3%' , paddingLeft:"30px"}}>#</th>
                                <th style={{ width: '15%' }}>Name</th>
                                <th style={{ width: '20%' }}>Wallet Address</th>
                                <th style={{ width: '14%' }}>Telegram</th>
                                <th style={{ width: '14%' }}>Twitter</th>
                                <th style={{ width: '10%' }}>Amount</th>
                                <th style={{ width: '14%' }}>Txn</th>
                            </tr>
                        </thead>
                        <tbody>
                            {props.history.length > 0 ? 
                                result.map((transaction, index) => {
                                    var wallet = transaction.wallet_address;
                                    wallet = wallet.slice(0, front) + " ..... " + wallet.slice(38);
                                    var txn = transaction.txn;
                                    txn = txn.slice(0, front) + " ..... " + txn.slice(62);
                                    return (
                                        <tr>
                                            <td style={{paddingLeft:"30px"}}>{props.history.length - index}</td>
                                            <td>{transaction.name}</td>
                                            <td>{wallet}</td>
                                            <td>{transaction.telegram}</td>
                                            <td>{transaction.twitter}</td>
                                            <td>{transaction.amount} BNB</td>
                                            <td><a href={'https://testnet.bscscan.com/tx/' + transaction.txn} target="_blank">{txn}</a></td>
                                        </tr>
                                    )
                                }) : null
                            }
                        </tbody>
                    </Table>
                </Row>
                <div className='pagination'>
                    <Stack style={{margin:"auto"}} spacing={1}>
                        <Pagination {...paginationConfig}/>
                    </Stack>
                </div>
            </div>
        </div>
    )   
}

export default HistoryTable;