import React from 'react'
import Footer_Logo from '../../../assets/images/Footer_Logo.svg';

export default function  MainFooter() {
  return (
    <div className='mainfooter'>
        <div className='logo_footer'><img src={Footer_Logo}/></div>
        <div className='footer_text_1'>BlockPortal</div>
        <div className='footer_text_2'>BlockPortal Copyright 2022</div>
        <div className='footer_text_3'>About</div>
        <div className='footer_text_4'>Private Sale</div>
    </div>
  )
}
