import React from 'react'
import { Chart as ChartJS, ArcElement, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';

ChartJS.register(ArcElement, Legend);

export default function PublicOffer() {
  const data = {
    datasets: [
      {
        data: [10, 90],
        backgroundColor: [
          '#FFFFFF',
          'rgba(0, 87, 255, 0.94)',
        ],
        borderColor: [
          '#FFFFFF',
          '#FFFFFF',
        ],
        borderWidth: 1,
        tooltip: { enable: false },
      },
    ],
  };
  return (
    <div className='public_offer'>
        <div className='pie_chart'>
          <Pie data={data}/>
          <div className='chart_pro'>10%</div>
        </div>
        
        <div className="token_text_3">10% Public Offered<br/><br/>{'\u2022'} Public Round(s)<br/>{'\u2022'} Public Sale Round(s)</div>
    </div>
  )
}
