import React from 'react'
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';
import PublicOffer from './sub/PublicOffer';
import CorporateHolding from './sub/CorporateHolding';

ChartJS.register(ArcElement, Tooltip, Legend);

const MainToken = () => {
    return (
        <div className="token_allocation">
            <div className='token_text_1'>Token Allocation</div>
            <div className='token_horizon'></div>
            <div className='token_vertical_1'></div>
            <div className='token_vertical_2'></div>
            <div className='token_vertical_3'></div>
            <span class="dot1"></span>
            <span class="dot2"></span>
            <span class="dot3"></span>
            <PublicOffer/>

            <CorporateHolding/>
        </div>
    );
}
 export default MainToken;